function deleteAcount(){
   
    //cast de json a objeto
    let ArticleObject= JSON.parse(localStorage.getItem("localArticleList")); //obtener lo que hay en el localstorage y guardarlo en una var
 
    ArticleObject = ArticleObject.filter(el => el.id != 0);
    localStorage.setItem("localArticleList", JSON.stringify(ArticleObject));
    

    location.reload();

}

